<template>
  <div v-if="isLoadingCurrentAdv">isLoading</div>
  <div v-else class="current">
    <div class="current__row">
      <div class="title">Имя</div>
      <div>{{ currentAdv.name }}</div>
    </div>
    <div class="current__row">
      <div class="title">Цена</div>
      <div>{{ currentAdv.price }}</div>
    </div>
    <div class="current__row">
      <div class="title">Ссылка</div>
      <div>{{ currentAdv.image }}</div>
    </div>
    <div class="current__row" v-if="currentAdv.description">
      <div class="title">Описание</div>
      <div>{{ currentAdv.description }}</div>
    </div>
    <div class="current__row" v-if="currentAdv.images ">
      <div class="title">Все ссылки</div>
      <div>
        <div
                v-for="image in currentAdv.images"
                :key="image"
        >
          {{ image }}
        </div>
      </div>
    </div>
  </div>
  <br>
  <lavel>
    Загрузить дополнительную информацию
    <input type="checkbox" v-model="isLoadAdditionalInfo"/>
  </lavel>
</template>

<script>


  import {mapActions, mapState} from "vuex";

export default {
  name: 'advView',
  data() {
    return {
      isLoadAdditionalInfo: false
    }
  },
  computed:{
    ...mapState(['currentAdv', 'isLoadingCurrentAdv'])
  },
  watch: {
    isLoadAdditionalInfo(value) {
      if (value) {
        this.loadCurrentAdv({id: this.$route.params.id, additionalFields: true})
      } else {
        this.loadCurrentAdv({id: this.$route.params.id})
      }
    }
  },
  created() {
    this.loadCurrentAdv({ id: this.$route.params.id})
  },
  methods:{
    ...mapActions(['loadCurrentAdv'])
  }

}
</script>
<style lang="scss">
  .current {
    width: 500px;
    &__row {
      display: flex;
      & div {
        flex: 0 0 1;
      }
    }
  }
  .title {
    display: inline-block;
    width: 100px;
  }
</style>
